// MAIN PORTFOLIO SINGLE :: START

.portfolioSingle {
  .main {
    position: relative;
    background-color: #39357a;


    &__bg {
      z-index: 0;
      overflow: hidden;
      position: absolute;
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }


    &__img {
      display: block;
      max-width: 100%;
      height: auto;
    }


    &__wrapper {
      z-index: 1;
      position: relative;
      display: flex;
      min-height: 800px;
      @include respond(m_m) {
        flex-wrap: wrap;
      }

      &-left,
      &-right {
        @include respond(m_m) {
          width: 100%;
        }
      }
      &-left {
        width: 70%;
      }
      &-right {
        width: 30%;
      }
    }


    &__title {
      padding-top: 169px;
      line-height: 1;
      color: $c-white;
      font-size: 120px;
    }


    &__subtitle {
      padding-left: 15px;
      padding-bottom: 32px;
      color: rgba($c-white, 0.5);
      font-size: 22px;
      font-family: "SF-Pro-Text", sans-serif;
    }


    &__btn {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 31px;
      margin-right: 8px;
      padding-left: 16px;
      padding-right: 16px;
      border-radius: 2em;
      background-color: rgba($c-white, 0.1);
      color: rgba($c-white, 0.5);
      @include transition(background-color, color);

      &:last-child {
        margin-right: 0;
      }
      &:hover {
        background-color: $c-white;
        color: $c-gunmetal;
        @include transition(background-color, color);
      }

      &-wrap {
        display: flex;
        align-items: center;
        padding-left: 15px;
      }
    }
  }
}

// MAIN PORTFOLIO SINGLE :: END